import React, { useEffect } from "react";
import "./transfer_Modal.css"
import Wait_Modal from "../../../contract/wait_Modal";
import { Link } from "react-router-dom";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useState } from "react";
import Qr_read from "./Qr_read";
const Transfer_Modal = (props) => {
    console.log(props.showFlag);
    const [target_address, setTarget_address] = useState("");
    const [wait_show, setWait_show] = useState(false);
    const active_waitModal = () => {
    
        setWait_show(true);
        props.setShowFlag(false);
    }
    const close_modal = () => {
        setWait_show(false);
    }


    const transfer = async () => {
        active_waitModal()
        props.cont.transfer(target_address,props.tokenId,close_modal) ;
       // active_waitModal()
    }
    return (

        <>
            {props.showFlag ? ( // showFlagtrueModal 
                <div id="overlay" onClick={() =>props.setShowFlag(false)}>
                    <div id="transfer" style={{"color":"#000000"}}>
                        <Form>
                            <Form.Group className="mb-3" controlId="form_titile" style={{ textAlign: "left" }}>
                                <Form.Label></Form.Label>
                                <Form.Control type="text" placeholder="Enter Title" value={target_address} onChange={(event) => setTarget_address(event.target.value)} />
                                <div style={{ textAlign: "right" ,margin:"10px" }}>
                                <Button variant="primary" onClick={() => {transfer()}} >
                                    
                                </Button>
                                </div>
                            </Form.Group>
                            
                        </Form>
                        <div className="row" style={{ textAlign: "right" }}>
                            <div className="col-2"/>
                            
                            <div className="col-8" style={{width:'100%', textAlign: "right" }}>
                                {props.setShowFlag ? <Qr_read  setTarget_address={ setTarget_address} />:<></>}
                                {/* <Qr_read  setTarget_address={ setTarget_address} /> */}
                            </div>
                            <div className="col-2"/>
                       
                        </div>
                </div>
                </div>
            ) : (
                <></>// showFlagfalseModal
            )}
            <Wait_Modal showFlag={wait_show} content={""} />
        </>
    );
};

export default Transfer_Modal;
