import React, { useState } from 'react';
import { QrReader } from 'react-qr-reader';

const Qr_read = ({setTarget_address}) => {
  const [data, setData] = useState('No result');
  const [state,setState]=useState(false);

  
  return (
    <>
      <QrReader
        onResult={(result, error) => {
          if (!!result) {
            setData(result?.text);
            let str = result?.text;
            let parts = str.split(':');
            let addressWithExtra = parts[1];

            let addressParts = addressWithExtra.split('@');
            let address = addressParts[0];
            setTarget_address(address);
          }

          if (!!error) {
            setData(result?.text);
            setState(error?.message);

            // console.info(error);
            console.log(error?.message)
          }
        }}
        style={{ width: '100%' }}
        constraints={{ facingMode: 'environment' }}
        videoStyle={{ width: '100%' }}
      />
        <p>{data}</p>
        <p>{state}</p>

    </>
  );
};

export default Qr_read;